.Dashboard{background-color:black;transition:opacity 0.25s ease-out;overflow:hidden}.Dashboard svg{filter:drop-shadow(0 0 0.5rem rgba(0,0,0,0.25))}.Dashboard a{color:inherit}.Dashboard.light{color:#111}.Dashboard.light .theme-fill{fill:#111}.Dashboard.light .theme-stroke{stroke:#111}.Dashboard.dark{color:white}.Dashboard.dark .theme-fill{fill:white}.Dashboard.dark .theme-stroke{stroke:white}
