.Overlay{position:absolute;top:0;left:0;margin:0.5rem;padding:0.5rem;border-radius:2rem;transition:all 0.25s ease-out;font-size:1.1rem}.Overlay .on-hover{opacity:0;transition:all 0.25s ease-out}.Overlay:hover{opacity:1;background-color:rgba(0,0,0,0.65)}.Overlay:hover .on-hover{opacity:1}.Overlay:hover svg{color:#3498db;filter:none}.Overlay a,.Overlay span{margin:0 0.25em}.Overlay svg,.Overlay i{transition:stroke 0.25s ease-out}.light .Overlay:hover{background-color:rgba(255,255,255,0.65)}
